
  @import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


/* Basic setup */
.App {
  font-family: 'Playfair Display', serif;
  background-color: transparent; /* Dark background */
  color: #f2f2f2; /* Light text */
}

/* Header */
.App-header {
  font-size: 2.5em;
  text-align: center;

  /* padding: 1em; */
  background: transparent; /* Slightly lighter than the main background */
  color: white;
}
h1{
  font-weight: 900;
}
p {
  font-weight: 500;
}
.about-blurb p {
  font-size: 1em;
  text-align: left;

  padding: 1em;
  background: transparent; /* Slightly lighter than the main background */
  color: white;
}

/* Cover Image */
.cover-image-section {
  height: 30vh; /* 30% of the viewport height */
}

.cover-image {
  background-image: url("./assets/images/Exhibition.jpg");
  background-size: cover;
  background-position: center;
  height: 100%;
}

body {
  /* Base color */

  background: linear-gradient(to top, #602f30, rgba(100, 66, 6, 0.638)),
    url(./assets/images/grainsvg.svg);
    margin: none;
    padding: none;
}

/* Gallery */
.container h1 {
  text-align: center;
 
}
.image-gallery {
  display: flex;
  flex-wrap: wrap;
  /* justify-content: center; */
  justify-content: space-between;
  gap: 20px;
  margin: none;

}

.image-gallery > li {
  flex-basis: 400px; 
  list-style-type: none;
  flex-grow: 1;

}

.image-gallery li img {
  object-fit: cover;
  max-width: 100%;
  height: auto;
  vertical-align: baseline;
  border-radius: 0px;
}

.image-gallery li {
  /* transition: transform 0.3s ease-in-out;  */
}

.image-gallery li:hover {
  /* transform: scale(1.05);  */
}

/* General styles for the About section */
.about-section h1 {
  padding-top: 15px;
 
  text-align: center;
}

.about-section {
  background: #8d7b68; 

  
}

/* Flexbox container for image and text */
.about-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
 
}

/* Image div */
.about-image img {
  max-width: 100%; /* To ensure the image doesn't exceed its container */
  height: auto;
  
  padding: 10px;
  margin: 20px;
  filter: saturate(0);
  border: #495734 2px solid;
  
}

/* Text div */
.about-text {
  font-size: 28px;
  flex: 1; /* Take up all available space */
  padding-left: 0px; /* Spacing between the image and text */
  padding-right: 40px;
}

/* Responsive design: Stacking for smaller screens */
@media (max-width: 768px) {
  .about-content {
    flex-direction: column;
    align-items: center;
  }
  .about-text {
    padding-left: 0; /* Reset padding */
    padding: 20px;
    text-align: left; /* Optional: Center-align text for smaller screens */
  }
  .about-blurb p {
    display: none;
  }
  .about-image img {
    padding: 0px;
    margin: 0px;
    border: none;
  }
}

/* Contact */
.contact-section {
  font-size: 1.5em;
  text-align: center;
  padding: 1em;
}

.contact-section a {
  padding: 10px;
}


/* Contact */
.contact-section {
  display: flex;
  flex-wrap: wrap;  /* Allow the flex items to wrap into multiple lines */
  text-align: center;
  padding: 1em;
  background-color: #602F30;
}

.contact-section a {
  flex: 1; /* Allow the flex items to grow and take up equal space */
  overflow: auto;
  padding: 10px 20px;
  margin: 0 10px;
  text-decoration: none;
  color: #FFF;
  border: 2px solid #FFF;
  background-color: rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}

.contact-section a:hover {
  background-color: #FFF;
  color: #602F30;
  border: 2px solid #602F30;
}

/* Media Queries */
@media (max-width: 768px) {
  .contact-section {
    flex-direction: column; /* Stack the flex items vertically */
  }

  .contact-section a {
    margin: 10px 0; /* Vertical margin instead of horizontal */
    font-size: 0.8em; /* Smaller text size */
  }
}


ul {
  margin: 0; /* To remove default bottom margin */ 
    padding: 0; /* To remove default left padding */
}